*,
*::after,
*::before {
  box-sizing: border-box;
}

html {
  box-sizing: inherit;
  font-family: "Montserrat", sans-serif;
  font-size: 62.5%;
  background-color: rgb(241, 245, 249);
}

body {
  font-size: 16px;
  margin-top: var(--space-top, 0px);
}

h1,
h2,
h3,
p,
a {
  padding: 0;
  margin: 0;
}

h1,
h2,
h3 {
  color: rgb(76, 5, 25);
  font-weight: 800;
}

h1 {
  font-size: 3.4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 3rem;
}

img {
  display: block;
  width: 100%;
}

a {
  text-decoration: none;
  color: rgb(255, 255, 255);
}

ul,
ol,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

.header {
  background-color: rgb(76, 5, 25);
  padding: 1rem 0;
  transition: all ease 150ms;
}
.header--fixed {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
}
.header__logo {
  width: 5rem;
  height: 5rem;
  background-color: rgb(255, 255, 255);
  border-radius: 50%;
  padding: 0.2rem;
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__navigation {
  display: flex;
  gap: 1.5rem;
}
.header__link {
  display: block;
  font-weight: 700;
  font-size: 1.4rem;
  padding: 0.5rem;
  border-radius: 0.5rem;
  background-color: rgb(76, 5, 25);
  transition: background-color ease 150ms;
}
.header__link--svg {
  display: block;
  height: 3.5rem;
  width: 3.5rem;
}
.header__link:hover {
  background-color: #1c0209;
}

.hero {
  background: url(/img/hero_1.webp);
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  min-height: 45rem;
  max-height: 50rem;
}
.hero__overlay {
  background-color: rgba(0, 0, 0, 0.6);
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.hero__container {
  padding: 4rem 0;
}
.hero__heading {
  color: rgb(255, 255, 255);
  font-size: 3rem;
}
@media (min-width: 768px) {
  .hero__heading {
    font-size: 4rem;
  }
}
@media (min-width: 992px) {
  .hero__heading {
    font-size: 5rem;
  }
}
.hero__subheading {
  color: rgb(241, 245, 249);
  font-size: 2rem;
  margin-top: 6rem;
  font-weight: 700;
  background-color: rgb(22, 78, 99);
  border-radius: 4rem;
  border: 0.25rem #0d2d39 solid;
  padding: 2rem;
  display: inline-block;
}
@media (min-width: 768px) {
  .hero__subheading {
    font-size: 3rem;
  }
}
@media (min-width: 992px) {
  .hero__subheading {
    font-size: 3.5rem;
  }
}

.hashtags__heading {
  text-align: center;
}
.hashtags__container {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
  align-items: center;
}
@media (min-width: 768px) {
  .hashtags__container {
    grid-template-columns: repeat(2, 1fr);
  }
}
.hashtags__section {
  background-color: rgb(255, 255, 255);
  padding: 2rem;
  border-radius: 1rem;
  box-shadow: 0 0 rgba(0, 0, 0, 0), 0 0 rgba(0, 0, 0, 0), 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -4px rgba(0, 0, 0, 0.1);
  border-left: 0.6rem solid rgb(80, 7, 36);
  display: grid;
  grid-template-columns: 1fr;
}
.hashtags__dual_section {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
}
.hashtags__paragraph {
  font-size: 1.8rem;
}
.hashtags__hashtag, .hashtags__phrase, .hashtags__hashtag--big {
  color: rgb(80, 7, 36);
  font-weight: bold;
}
.hashtags__hashtag--big {
  margin-bottom: 2rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.hashtags__phrase {
  font-size: 1.8rem;
}
.hashtags__areas {
  padding-left: 2rem;
  margin: 2rem 0 0 0;
}
.hashtags__area {
  list-style: square;
  font-size: 2rem;
}
.hashtags__area::marker {
  color: rgb(76, 5, 25);
}

.about {
  background: url(/img/hero_2.webp);
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 60rem;
  position: relative;
}
@media (min-width: 768px) {
  .about {
    min-height: 50rem;
  }
}
@media (min-width: 992px) {
  .about {
    min-height: 40rem;
  }
}
.about__overlay {
  background-color: rgba(0, 0, 0, 0.6);
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.about__container {
  padding: 2rem 0;
}
.about__heading {
  margin-bottom: 4rem;
  color: rgb(241, 245, 249);
}
.about__paragraph {
  font-size: 1.8rem;
  font-weight: 600;
  color: rgb(255, 255, 255);
  padding: 2rem;
  border-radius: 1rem;
  background-color: rgb(80, 7, 36);
  border-left: 0.6rem rgb(255, 255, 255) solid;
}

.specialties__heading {
  text-align: center;
}
.specialties__container {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
}
@media (min-width: 768px) {
  .specialties__container {
    grid-template-columns: repeat(2, 1fr);
    padding: 5rem 0;
  }
}
.specialties__item, .specialties__item--bottom, .specialties__item--top {
  display: grid;
  gap: 2rem;
  padding: 2rem;
  border-radius: 1rem;
  background-color: white;
  border-left: 0.6rem solid rgb(80, 7, 36);
  box-shadow: 0 0 rgba(0, 0, 0, 0), 0 0 rgba(0, 0, 0, 0), 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -4px rgba(0, 0, 0, 0.1);
}
@media (min-width: 768px) {
  .specialties__item--top {
    transform: translateY(-5rem);
  }
}
@media (min-width: 768px) {
  .specialties__item--bottom {
    transform: translateY(5rem);
  }
}
.specialties__name {
  font-size: 2.4rem;
}
.specialties__paragraph {
  font-size: 1.8rem;
}

.footer {
  background-color: rgb(22, 78, 99);
  padding: 4rem 0 0;
}
.footer__container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 4rem 0;
  gap: 5rem;
}
@media (min-width: 480px) {
  .footer__container {
    flex-direction: row;
  }
}
.footer__logos {
  display: flex;
  flex-direction: column;
  padding: 2rem;
  border-radius: 1rem;
  background-color: rgb(241, 245, 249);
  gap: 2rem;
}
@media (min-width: 768px) {
  .footer__logos {
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }
}
.footer__logo {
  max-width: 25rem;
}
@media (min-width: 768px) {
  .footer__logo {
    width: auto;
    max-height: 10rem;
  }
}
.footer__navigation {
  display: flex;
  gap: 2rem;
  align-items: center;
}
.footer__link {
  background-color: rgb(22, 78, 99);
  padding: 1rem;
  display: block;
  border-radius: 1rem;
  transition: background-color ease 150ms;
}
.footer__link--svg {
  width: 5rem;
  height: 5rem;
}
.footer__link:hover {
  background-color: #0d2d39;
}
.footer__copy {
  background-color: rgb(76, 5, 25);
  display: block;
  color: rgb(255, 255, 255);
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  padding: 1rem 2rem;
}

.container {
  max-width: 120rem;
  width: 90%;
  margin: 0 auto;
}

.section {
  margin-top: 5rem;
  margin-bottom: 5rem;
}

.container-sm {
  max-width: 80rem;
  width: 90%;
  margin: 0 auto;
}